.header {
  display: flex;
  justify-content: space-between;
  position: fixed;
  z-index: 5;
  transition: transform 1s;
}

.header--top {
  width: 100%;
  padding-top: 43px;
  padding-left: 5%;
  padding-right: 5%;
  top: 0;
  left: 0;
}

