  .header--top {
    padding-top: 22px;
  }
}

.header--right {
  padding-top: 43px;
  right: 0;
  top: 0;
  width: 10%;
  height: 50%;
  flex-direction: column;
}

.header__link {
  position: relative;
  width: 34px;
  height: 34px;
  overflow: hidden;
  animation-delay: 0.3s;
}

.header__link span,
.header__link svg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  transition: width 0.2s, height 0.2s;
}

.header__link--main span {
  background-color: #0d71fc;
  border-radius: 50%;
  width: 18px;
  height: 18px;
}

.header__link--main:hover span {
  width: 22px;
  height: 22px;
}

.header__link--main:active span {
  width: 14px;
  height: 14px;
}

.header__link--works:hover svg {
  width: 26px;
  height: 26px;
}

.header__link--works:active svg {
  width: 18px;
  height: 18px;
}

.header__link--menu svg {
  width: 26px;
  height: 15px;
}

.header__link--menu:hover svg {
  width: 30px;
  height: 15px;
}

.header__link--menu:active svg {
  width: 22px;
  height: 15px;
}

